import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
// in development env not use Lazy Loading,because Lazy Loading too many pages will cause webpack hot update too slow.so only in production use Lazy Loading

/* layout */
import Layout from '../views/layout/Layout'

/* login */
const Login = _import('login/index')

/* dashboard */
const dashboard = _import('dashboard/index')

/* error page */
const Err404 = _import('404')

/* demo page */
const Form = _import('page/form')
const Table = _import('table/index')
const BugLevel = _import('basic/bugLevel')
const FunctionType = _import('basic/functionType')
const DifficultLevel = _import('basic/difficultLevel')
const Employee = _import('basic/employee')
const Page = _import('basic/page')
const Project = _import('basic/project')
const Module = _import('basic/module')

Vue.use(Router)

 /**
  * icon : the icon show in the sidebar
  * hidden : if `hidden:true` will not show in the sidebar
  * redirect : if `redirect:noredirect` will not redirct in the levelbar
  * noDropdown : if `noDropdown:true` will not has submenu in the sidebar
  * meta : `{ role: ['admin'] }`  will control the page role
  **/
export const constantRouterMap = [
  { path: '/login', component: Login, hidden: true },
  { path: '/404', component: Err404, hidden: true },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    name: 'Home',
    hidden: true,
    children: [{ path: 'dashboard', component: dashboard }]
  }
]

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  {
    path: '/example',
    component: Layout,
    redirect: 'noredirect',
    name: 'Example',
    icon: 'zujian',
    children: [
      { path: 'index', component: Form, name: 'Form', icon: 'zonghe' }
    ]
  },

  {
    path: '/table',
    component: Layout,
    redirect: '/table/index',
    icon: 'tubiao',
    noDropdown: true,
    children: [{ path: 'index', component: Table, name: 'Table', meta: { role: ['admin'] }}]
  },

  {
    path: '/basic',
    component: Layout,
    redirect: 'noredirect',
    name: '基础信息',
    icon: 'shouce',
    children: [
      { path: 'bugLevel', component: BugLevel, name: 'Bug等级', icon: 'bug' },
      { path: 'functionType', component: FunctionType, name: '功能类型', icon: 'functionType' },
      { path: 'difficultLevel', component: DifficultLevel, name: '功能难度等级', icon: 'difficultLevel' },
      { path: 'employee', component: Employee, name: '员工信息', icon: 'employee' },
      { path: 'page', component: Page, name: '页面信息', icon: 'page' },
      { path: 'project', component: Project, name: '项目信息', icon: 'project' },
      { path: 'module', component: Module, name: '模块信息', icon: 'module' }
    ]
  },
  { path: '*', redirect: '/404', hidden: true }
]



